.contactList {
    list-style: none;
    padding   : 0;
    margin    : 0;
}

.contactListItem {
    display    : flex;
    align-items: center;
    text-align : left;
}

.removeBtn {
    margin          : 1px 5px;
    background-color: tomato;
    border          : none;
    border-radius   : 4px;
    color           : white;
    font-size       : 10px;
    width           : 18px;
    height          : 18px;
    padding         : 0;
    cursor          : pointer;
}